import React, { useState, useEffect } from "react";
import axios from "axios";
import {
  Paper,
  Box,
  AppBar,
  Grid,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Typography,
  DialogContentText,
} from "@mui/material";
import ChecklistIcon from "@mui/icons-material/Checklist";

import CheckableList from "./CheckableList";
import CheckHistory from "./CheckHistory";

export default function LBapprove({ tabIndex }) {
  const [fileList, setFileList] = useState([]);
  const [historyList, setHistoryList] = useState([]);
  const [checked, setChecked] = useState([]);
  const [open, setOpen] = useState(false);

  // checkbox
  const handleToggle = (value) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
  };

  // Dialog
  const handleApprove = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  // 
  const loadcheck = async () => {
    try {
      const response = await axios.get("/api/files/?check=false");
      setFileList(response.data.files);
    } catch (error) {
      console.error("Error fetching files:", error);
    }
  };

   // 
   const loadHistory = async () => {
    try {
      const response = await axios.get("/api/files/?check=true");
      const filteredFiles = response.data.files.filter(file => file.filename !== "SystemTest.pdf");
      setHistoryList(filteredFiles);
    } catch (error) {
      console.error("Error fetching files:", error);
    }
  };

  // 
  useEffect(() => {
    loadcheck();
    loadHistory();
  
    const intervalId = setInterval(() => {
      loadcheck();
      loadHistory();
    }, 10000);

    return () => clearInterval(intervalId);
  }, []); 

  // 
  const handleSubmit = async () => {
    try {
      const response = await axios.post("/api/checked", {
        googleIds: checked,
      });
      console.log("res", response.data);
      alert("!");
      setChecked([]);
      loadcheck();
    } catch (error) {
      console.error("err", error);
      alert("!");
    }
    setOpen(false);
  };

  return (
    <>
      {tabIndex === 0 && (
        <Paper sx={{ maxWidth: 936, margin: "auto", overflow: "hidden" }}>
          <Box sx={{mx:4 , my:2}}>
            <AppBar
              position="static"
              color="transparent"
              elevation={0}
              sx={{ borderBottom: "1px solid rgba(0, 0, 0, 0.12)" }}
            >
              <Grid sx={{pl:0}}>
                <Grid container spacing={2} alignItems="center">
                  <Grid item>
                    <ChecklistIcon
                      color="secondary"
                      fontSize="large"
                      sx={{ mt: 1 }}
                    />
                  </Grid>
                  <Grid item xs>
                    <Typography variant="h6"></Typography>
                  </Grid>
                  <Grid item>
                    <Button
                      variant="contained"
                      onClick={handleApprove}
                      sx={{ mr: 1 }}
                    >
                      Approve
                    </Button>
                  </Grid>
                </Grid>
              </Grid>
            </AppBar>
            <CheckableList
              items={fileList}
              checked={checked}
              onToggle={handleToggle}
            />
            <Dialog open={open} onClose={handleClose}>
              <DialogTitle></DialogTitle>
              <DialogContent>
                <DialogContentText></DialogContentText>
                <DialogContentText>
                  {checked.length > 0 ? (
                    checked.map((id) => {
                      const item = fileList.find(
                        (item) => item.googleId === id
                      );
                      return (
                        <div key={id}>
                          {item ? item.filename : ""}
                        </div>
                      );
                    })
                  ) : (
                    <div></div>
                  )}
                </DialogContentText>
              </DialogContent>
              <DialogActions>
                <Button
                  onClick={() => {
                    handleClose();
                  }}
                >
                  
                </Button>

                <Button
                  onClick={() => {
                    handleSubmit();
                    setOpen(false);
                  }}
                >
                  
                </Button>
              </DialogActions>
            </Dialog>
          </Box>
        </Paper>
      )}

      {tabIndex === 1 && (
        <Paper sx={{ maxWidth: 936, margin: "auto", overflow: "hidden" }}>
          <CheckHistory history={historyList} />
        </Paper>
      )}
    </>
  );
}
