import React, { useState, useEffect } from "react";
import axios from "axios";
import PdfViewer from "./Pdfviewer";

import "./labbook.css";
import {
  TextField,
  Button,
  Box,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Grid,
  Typography,
} from "@mui/material";
import UploadFileIcon from "@mui/icons-material/UploadFile";
import Filelist from "./Uploadhistory";

function Labbook({ userInfo, tabIndex }) {
  const fullname = userInfo ? userInfo.fullname : "Unknown";
  const username = userInfo ? userInfo.username : "Unknown";
  const [experimentDetail, setExperimentDetail] = useState("");
  const [uploadFile, setUploadFile] = useState(null);
  const [uploadHistory, setUploadHistory] = useState([]);
  const [openPop, setOpenPop] = useState(false);
  const [uploadname, setUploadname] = useState("");

  // 
  const handleFileSelect = (file) => {
    setUploadFile(file);
  };

  // 
  const handleSubmit = (event) => {
    event.preventDefault();
    const today = new Date();
    const formattedDate = `${today.getFullYear()}${(today.getMonth() + 1)
      .toString()
      .padStart(2, "0")}${today.getDate().toString().padStart(2, "0")}`;
    const uploadname = `${formattedDate}-${username}-${fullname}-${experimentDetail}.pdf`;
    setUploadname(uploadname);
    setOpenPop(true);
  };

  // 
  const handleConfirm = () => {
    const formData = new FormData();
    formData.append("file", uploadFile);
    formData.append("uploadname", uploadname);

    axios
      .post("/api/upload", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then((response) => {
        console.log("Upload successful:", response.data);
        alert("File uploaded successfully!");
      })
      .catch((error) => {
        console.error("Upload error:", error);
        alert("Failed to upload file.");
      });
    setOpenPop(false);
    setUploadname("");
  };
  const handleClose = () => {
    setOpenPop(false);
  };

  // 
  const loadUpload = async () => {
    try {
      const response = await axios.get(`/api/files/?username=${username}`);
      setUploadHistory(response.data.files);
    } catch (error) {
      console.error("Error fetching files:", error);
    }
  };

  useEffect(() => {
    loadUpload();

    const intervalId = setInterval(() => {
      loadUpload();
    }, 10000);

    return () => clearInterval(intervalId);
  }, []);

  return (
    <>
      {tabIndex === 0 && (
        <Box component="form" onSubmit={handleSubmit} className="labbook">
          <Grid sx={{ mx: 4, my: 2 }}>
            <Grid container alignItems={"center"}>
              <Grid item xs={12}>
                <Grid
                  container
                  sx={{ justifyContent: "space-between", alignItems: "center" }}
                >
                  <Grid item>
                    <Typography
                      variant="h5"
                      sx={{ fontWeight: "bold", margin: 0, display: "flex" }}
                    >
                      <UploadFileIcon fontSize="large" sx={{ mr: 1 }} />{" "}
                      
                    </Typography>
                  </Grid>
                  <Grid item>
                    <Button
                      variant="contained"
                      component="a"
                      href="https://docs.google.com/document/d/1vsBmcYMHDHo0BA_AmXprkDFd_IFY6qKrm50qy5Eg-30/edit?usp=share_link" //  URL
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      
                    </Button>
                  </Grid>
                </Grid>
              </Grid>

              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  label=""
                  value={experimentDetail}
                  onChange={(e) => setExperimentDetail(e.target.value)}
                  InputProps={{
                    style: { color: "#C0C0C0" },
                  }}
                  InputLabelProps={{
                    style: { color: "#C0C0C0" },
                  }}
                  fullWidth
                  sx={{
                    mt: 2,
                    "& .MuiOutlinedInput-root": {
                      "& fieldset": {
                        borderColor: "#2e2e2e",
                      },
                      "&:hover fieldset": {
                        borderColor: "secondary.main",
                      },
                      "&.Mui-focused fieldset": {
                        borderColor: "primary.main",
                      },
                    },
                  }}
                />
              </Grid>

              <Grid item xs={12} sx={{ mb: 6, mt: 3 }}>
                <PdfViewer onFileSelect={handleFileSelect} />
              </Grid>

              <Grid item xs={12} container justifyContent={"center"}>
                <Button
                  className="submit-btn"
                  type="submit"
                  variant="contained"
                  color="primary"
                  sx={{ my: 2 }}
                >
                  
                </Button>
              </Grid>
            </Grid>
          </Grid>

          <Dialog open={openPop} onClose={handleClose}>
            {!uploadFile || !experimentDetail ? (
              <>
                <DialogTitle>{""}</DialogTitle>
                <DialogContent>
                  <DialogContentText>
                    !
                  </DialogContentText>
                </DialogContent>
              </>
            ) : (
              <>
                <DialogTitle>{""}</DialogTitle>
                <DialogContent>
                  <DialogContentText>
                    :
                  </DialogContentText>
                  <DialogContentText>
                    {uploadname} Google Drive
                  </DialogContentText>
                </DialogContent>
                <DialogActions>
                  <Button onClick={handleClose} color="primary">
                    
                  </Button>
                  <Button onClick={handleConfirm} color="primary" autoFocus>
                    
                  </Button>
                </DialogActions>
              </>
            )}
          </Dialog>
        </Box>
      )}
      {tabIndex === 1 && (
        <Box sx={{ mx: 4, my: 2 }}>
          <Filelist items={uploadHistory} />
        </Box>
      )}
    </>
  );
}

export default Labbook;
